.App {
 display: flex;
}

.chat-users{
  width: 200px;
  border-right: 1px solid #ccc;
  padding: 10px;
}

.user-item{
  cursor: pointer;
  padding: 5px;
}

.chat-container{
  flex: 1;
  display: flex;
  flex-direction: column;
}

.chat-window{
  flex: 1;
  padding: 10px;
  border-bottom: 1px solid #ccc;
  overflow-y: auto;
}

.text-input{
  padding: 10px;
  border-top: 1px solid #ccc;
  display: flex;
}

.text-input input {
  flex: 1;
  padding: 5px;
}

.text-input button {
  padding: 5px 10px;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
